import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import http from '@/http/interface.js'
export default {
	components: {cmdNavBar},
	data() {
		return {
			userPhone : '',
			codeTime : '获取',
			newPass1 : '',
			newPass2 : '',
			phoneCode : ''
		};
	},
	methods:{
		goBack(){
			if(!(/^1[3456789]\d{9}$/.test(this.userPhone))){ 
				uni.showToast({
					title: '手机号码输入有误',
					icon: 'none',
					duration: 2500
				})
				return; 
			}
			if(this.phoneCode == ''){
				uni.showToast({
					title: '短信验证码不能为空',
					icon: 'none',
					duration: 2500
				})
				return;
			}
			if(this.newPass1 != this.newPass2 || this.newPass1 == ''){
				uni.showToast({
					title: '两次输入密码不一致',
					icon: 'none',
					duration: 2500
				})
				return;
			}
			http.get('member/forgetPwd',{phone:this.userPhone,newPwd:this.newPass1,phoneCode:this.phoneCode}).then((res)=>{
				uni.reLaunch({
					url : '/pages/checkpoint/login/login?msg=修改密码成功'
				})
			})
		},
		getPhoneCode(){
			if(!(/^1[3456789]\d{9}$/.test(this.userPhone))){ 
				uni.showToast({
					title: '手机号码输入有误',
					icon: 'none',
					duration: 2500
				})
				return; 
			}
			if(this.codeTime != '获取'){
				return;
			}
			uni.showLoading({
				title : '发送中',
				mask:true
			})
			http.get('member/getPhoneCode',{phone:this.userPhone}).then((res)=>{
				uni.showToast({
					title: '验证码发送成功',
					icon: 'none',
					duration: 2500
				})
				let _this = this;
				let t = 60;
				_this.codeTime = '60s';
				let codeSet = setInterval(()=>{
					t--;
					_this.codeTime = t + 's';
					if(t == 1){
						_this.codeTime = '获取';
						clearInterval(codeSet)
					}
				},1000)
				
			})
		}
	}
}
